import React from 'react'
import { Container } from 'react-bootstrap'
import styled from 'styled-components'
import { colors } from '../elements/global'
import Logo from '../assets/logo-icon.svg'
const StylesFooter = styled.div`
    .footer{
        background-color: ${colors.navy};
        height: 300px;
        padding: 40px;
        color: #ffffff;
    }
    .head-footer{
        font-weight: 200;
    }
    .head-list-footer{
        font-weight: 700;
        margin-bottom: 15px;
    }
    .foot-link{
        list-style: none;
        line-height: 30px;
        color: gray;
    }
    .foot-link-icon{
        font-size: 24px;
        list-style: none;
        letter-spacing: 15px;
    }
    @media only screen and (max-width: 768px) {
        /* For mobile phones: */
        .footer{
            height: 400px;
            padding: 0;
            padding-top: 35px;
        }
        .head-footer{
            text-align: center;
        }
        .head-list-footer-icon ,.foot-link-icon{
            margin-left: -30px;
        }
        .list-footer{
            float: left;
            width: 50%;   
            padding-top: 20px;   
        }
    }
`

export const Footer = () => (
    <StylesFooter>
        <div className="footer">
            <Container>
                <div className="row">
                    <div class="col-sm-4 head-footer">
                        <p><img className="mr-3" src={Logo} alt="Logo Icon" width="32px" />Gulphy Bikes</p>
                        <p>Copyright &copy; 2020 Gulphy Bikes.</p>
                    </div>
                    <div className="col-sm-4 list-footer">
                        <ul>
                            <h6 className="head-list-footer">SHOP</h6>
                            <a href="/"> <li className="foot-link">Trakker</li></a>
                            <a href="/"> <li className="foot-link">Thril</li></a>
                            <a href="/"> <li className="foot-link">United</li></a>
                            <a href="/"> <li className="foot-link">Polygon</li></a>

                        </ul>
                    </div>
                    <div className="col-sm-4 list-footer">
                        <ul>
                            <h6 className="head-list-footer-icon">SOCIAL MEDIA</h6>
                            <li className="foot-link-icon">
                                <a href="/"><i class="fa fa-instagram"></i></a>
                                <a href="/"><i class="fa fa-twitter"></i></a>
                                <a href="/"><i class="fa fa-github"></i></a>
                                <a href="/"><i class="fa fa-google-plus"></i></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </Container>
        </div>
    </StylesFooter >
)