import React, { Component } from 'react'
import { Container } from 'react-bootstrap'
import { LayoutContent } from '../elements/LayoutContent'
import { ImageHistory } from '../elements/element'

export class History extends Component {
    render() {
        return (
            <LayoutContent>
                <Container id="About">
                    <h2 className="text-center">HISTORY</h2>
                    <div className="row">
                        <div class="col-lg-6">
                            <ImageHistory alt="Trakne"></ImageHistory>
                        </div>
                        <div class="col-lg-6">
                            <h4 className="text-center">1975–1979 — The early years</h4>
                            <p className="text-justify">
                                In December, 1975, Richard (Dick) Burke and Bevil Hogg established Trek Bicycle as a wholly owned subsidiary of Roth Corporation, a Milwaukee-based appliance distributor. In early 1976, with a payroll of five, Trek started manufacturing steel touring frames in Waterloo, Wisconsin, taking aim at the mid to high-end market dominated by Japanese and Italian made models. Trek built nearly 900 custom hand-brazed framesets that first year, each selling for just under $200. Later that same year Trek Bicycle was incorporated. In 1977, Penn Cycle in Richfield, Minnesota became the first Trek retailer in the world. Within three years, Trek sales approached $2,000,000. </p>
                            <h4 className="text-center">1980–1984 — Trek becomes a business</h4>
                            <p className="text-justify">
                                Hampered without additional manufacturing capacity, Trek sales plateaued in the late 1970s. In just a few years Trek had outgrown its original "red barn" manufacturing facility—a former carpet warehouse. Recognizing the need for expansion, in 1980 Trek broke ground on a new 26,000 sq ft (2,400 m2) corporate headquarters on the outskirts of Waterloo.
                            </p>
                        </div>
                    </div>
                </Container>
            </LayoutContent>
        )
    }
}