import styled, { css } from 'styled-components'
import { colors } from '../elements/global'
import bikeImage from '../assets/trak-maddne.jpg'
import bikeImage3 from '../assets/trak-maddne3.jpg'
import bikeImage4 from '../assets/trak-maddne4.jpg'

//Elements Home
export const Button = styled.button`
    height: 40px;
    border-radius: 32px;
    padding: 0 28px;
    ${props => props.width && css`
        width: 140px;
    `}
    ${props => props.primary && css`
        background: ${colors.primary};
        &:hover{
        background: #054721;
    }
    `}
    ${props => props.secondary && css`
        background: ${colors.secondary};
        &:hover{
        background: #722b0e;
    }
    `}
    ${props => props.spacing && css`
        letter-spacing: 5px;
    `}
    color: #ffff;
    border: none;   
`
export const StylesHome = styled.div`
    .col-lg-6{
        padding-top: 55px;
    }
    .carousel-indicators{
        visibility: hidden;
    }
    .carousel-control-prev{
        filter: invert(100%);
        margin-left: -100px;
    }
    .carousel-control-next{
        filter: invert(100%);
        margin-right: -50px;
    }

    @media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .btn-sec{
       margin-left: 25%; 
       margin-top: 5%;
    }
    .carousel-control-prev{
        filter: invert(100%);
        margin-left: -20px;
    }
    .carousel-control-next{
        filter: invert(100%);
        margin-right: -20px;
    }
    
}`
export const FontStyles = styled(StylesHome)`
    /* color: ${colors.brown}; */
    ${props => props.brown && css`
        color: ${colors.brown};
        font-weight: 700;
    `}
    ${props => props.navy && css`
        color: ${colors.navy};
    `}
    font-size: 65px;
    @media only screen and (max-width: 768px) {
        /* For mobile phones: */
            font-size: 55px;
            padding-left: 20px; 
    }
`
export const CarouselImage = styled.img.attrs({
    src: bikeImage
})`
    width: 500px;
    max-width: 500px;
    @media only screen and (max-width: 768px) {
    /* For mobile phones: */
        width: 400px;
    } 
`
export const SubHead = styled.p`
    max-width: 370px;
    line-height: 28px;
    text-align: justify;
    @media only screen and (max-width: 768px) {
    /* For mobile phones: */
        padding-left: 20px;
}
`

// Elements History
export const ImageHistory = styled.img.attrs({
    src: bikeImage3
})`
    width: 500px;
    @media only screen and (max-width: 768px) {
    /* For mobile phones: */
        max-width: 360px;
    }
`

// Elements Technology
export const StylesTechnology = styled.div`
    .jumbo{
        background: url(${bikeImage3}) no-repeat fixed center;
        background-size: cover;
        color: #efefef;
        height: 500px;
        position: relative;
        z-index: -1;

    }
    .overlay{
        background-color: #000;
        opacity: 0.8;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: -1;
    }
    .container{
        padding-top: 80px;
    }
    @media only screen and (max-width: 768px) {
        /* For mobile phones: */
        .container{
            max-width: 400px;
            text-align: center;
        }
    }
    
`;

//Element Production
export const ImageProduction = styled.img.attrs({
    src: bikeImage4
})`
    width: 500px;
    border-radius: 40px 0 40px 40px;
    @media only screen and (max-width: 768px) {
        /* For mobile phones: */
            max-width: 360px; 
    }
`

//Element Review
export const StylesReview = styled.div`
    .card{
        border-radius: 40px;
    }
    .checked{
        color: orange;
    }
`