import React from 'react'
import { Container } from 'react-bootstrap'
import Breadcrumb from 'react-bootstrap/Breadcrumb'
import styled from 'styled-components'
import { NavigationBarShop } from '../components/NavigationBar'
import { Footer } from '../components/Footer'
import { Card } from 'react-bootstrap'
import { Button } from '../elements/element'
// import testGambar from '../assets/imgsale.jpg'

const StyleShop = styled.div`
    .row{
        padding-bottom: 20px;
    }
    .breadcumb{
        padding-top: 35px;
        float: left;
    }
    .card{
        border-radius: 20px;
    }
    .cart-icon{
        font-size: 34px;
        color: gray;
    }
    .badge {
        -webkit-border-radius: 9px;
        -moz-border-radius: 9px;
        border-radius: 9px;
    }
    #lblCartCount {
        font-size: 12px;
        background: #eab759;
        color: #000;
        padding: 0 5px;
        vertical-align: top;
        margin-left: -10px;
    }
`


export class Shop extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            order: 0,
            name: '',
        }
    }
    componentDidMount() {

    }
    handleAddtoCart = () => {
        this.setState({
            order: this.state.order + 1,
            name: 'hello guys'
        })
    }
    render() {
        return (
            <StyleShop>
                <NavigationBarShop />
                <Container>
                    <div className="row">
                        <div className="col-lg-12 breadcumb">
                            <Breadcrumb>
                                <Breadcrumb.Item href="/">Home</Breadcrumb.Item>
                                <Breadcrumb.Item active>Shop</Breadcrumb.Item>
                                <a href="/" className="shop-cart ml-auto">
                                    <i className="fa fa-shopping-cart cart-icon"></i>
                                    <span className="badge badge-info" id="lblCartCount">{this.state.order}</span>
                                </a>
                            </Breadcrumb>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-lg-3">
                            <Card style={{ width: "18rem" }}>
                                <img className="card-img-top" src="https://via.placeholder.com/150" alt="Card cap" />
                                <div className="card-body">
                                    <h5 className="card-title">MEME</h5>
                                    <p className="card-text">Lorem ipsum dolor si goblog </p>
                                    <Button onClick={this.handleAddtoCart} secondary>Add to Cart</Button>
                                </div>
                            </Card>
                        </div>
                    </div>
                </Container>
                <Footer />
            </StyleShop>
        )
    }
}