import React, { Component } from 'react'
import { Jumbotron as Jumbo, Container } from 'react-bootstrap'
import { StylesTechnology } from '../elements/element'

export class Jumbotron extends Component {
    render() {
        return (
            <StylesTechnology>
                <Jumbo fluid className="jumbo" id="Technology">
                    <div className="overlay"></div>
                    <Container>
                        <h1>Technology</h1>
                        <p>In 2014, the UCI unified the two classifications into a single classification in line with regulations for current track pursuit bikes. Following the change in the rules, German Jens Voigt became the first rider to attempt the hour record, on 18 September 2014 at the Velodrome Suisse, Grenchen, Switzerland on a Trek bicycle. He set a new record of 51.110 km, beating the previous record set by Sosenka by 1.410 km.</p>
                    </Container>
                </Jumbo>
            </StylesTechnology>
        )
    }
}

