import React from 'react'
import { Nav, Navbar, Container } from 'react-bootstrap'
import styled from 'styled-components'
import { Button } from '../elements/element'
import Logo from '../assets/logo-icon.svg'
import { Link } from "react-scroll";

const Styles = styled.div`
    .navbar {
        background-color: #ffffff;
        padding-top: 28px;
        padding-bottom: 20px;
        font-family: 'Nunito Sans', sans-serif
    }
    .navbar-brand{
        font-weight: 400;
    }
    .nav-link{
        margin-right: 20px;
        font-size: 14px;
        font-weight: 700;
        cursor: pointer;
    }
    
    
`;

export const NavigationBar = () => {

    return (
        <Styles>
            <Navbar expand="lg">
                <Container>
                    <Navbar.Brand href="/"><img className="mr-3" src={Logo} alt="Logo Icon" width="32px" />Gulphy Bikes</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <Nav.Item><Nav.Link href="/Shop">
                                Shop
                                </Nav.Link></Nav.Item>
                            <Nav.Item><Link className="nav-link" to="About"
                                spy={true}
                                smooth={true}
                                duration={900} activeClass="active"
                            >History</Link></Nav.Item>
                            <Nav.Item><Link className="nav-link" activeClass="active"
                                to="Technology"
                                spy={true}
                                smooth={true}
                                duration={900}>Technology</Link></Nav.Item>
                            <Nav.Item><Link className="nav-link" activeClass="active"
                                to="Production"
                                spy={true}
                                smooth={true}
                                offset={-30}
                                duration={900}>Production</Link></Nav.Item>
                            <Nav.Item><Link className="nav-link" activeClass="active"
                                to="Review"
                                spy={true}
                                smooth={true}
                                offset={-30}
                                duration={900}>Reviews</Link></Nav.Item>
                        </Nav>
                        <Nav className="ml-auto">
                            <a href="/no"><Button primary width>Login</Button></a>

                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </Styles>
    )
}

export const NavigationBarShop = () => {
    return (
        <Styles>
            <Navbar expand="lg">
                <Container>
                    <Navbar.Brand href="/"><img className="mr-3" src={Logo} alt="Logo Icon" width="32px" />Gulphy Bikes</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <Nav.Item><Nav.Link href="/">
                                Home
                                </Nav.Link></Nav.Item>
                            <Nav.Item><Nav.Link href="/#About">
                                History
                                </Nav.Link></Nav.Item>
                            <Nav.Item><Nav.Link href="/#Technology">
                                Technology
                                </Nav.Link></Nav.Item>
                            <Nav.Item><Nav.Link href="/#Production">
                                Production
                                </Nav.Link></Nav.Item>
                            <Nav.Item><Nav.Link href="/#Review">
                                Reviews
                                </Nav.Link></Nav.Item>
                        </Nav>
                        <Nav className="ml-auto">
                            <a href="/no"><Button primary width>Login</Button></a>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </Styles>
    )
}
