import React from 'react'
import styled from 'styled-components'

const Styles = styled.div`
    .container{
        padding-top: 30px;
        padding-bottom: 50px;
    }
    .text-center{
        font-weight: 700;
    }
    @media only screen and (max-width: 768px) {
        /* For mobile phones: */
        .container{
            max-width: 380px;
            text-align: center;
        }
    }
`

export const LayoutContent = (props) => (
    <Styles>
        {props.children}
    </Styles>
)