import React, { Component } from 'react'
import { Container, Card, CardDeck, } from 'react-bootstrap'
import { LayoutContent } from '../elements/LayoutContent'
import Logo from '../assets/logo-icon.svg'
import { Stars } from '../elements/global'
import { StylesReview } from '../elements/element'


export class Review extends Component {
    render() {
        return (
            <LayoutContent>
                <Container id="Review">
                    <StylesReview>
                        <h2 className="text-center">REVIEW</h2>
                        <div className="row pt-5">
                            <div class="col-lg-4 ">
                                <CardDeck>
                                    <Card>
                                        <div className="card-body">
                                            <h5 class="card-title"><img className="mr-3" alt="hw" width="32px" src={Logo}></img>Ihsan Sayid</h5>
                                            <p class="card-text">Sepedanya sangat bagus dan menawan untuk dipakai berenang.</p>
                                            <Stars></Stars>
                                        </div>
                                    </Card>
                                </CardDeck>
                            </div>
                            <div class="col-lg-4">
                                <CardDeck>
                                    <Card>
                                        <div className="card-body">
                                            <h5 class="card-title"><img className="mr-3" alt="hw" width="32px" src={Logo}></img>Boue Adams</h5>
                                            <p class="card-text">Memilih sepeda anak yang cocok tidak hanya sekedar karena faktor harga dan merk sepeda. </p>
                                            <Stars></Stars>
                                        </div>
                                    </Card>
                                </CardDeck>
                            </div>
                            <div class="col-lg-4">
                                <CardDeck>
                                    <Card>

                                        <div className="card-body">
                                            <h5 class="card-title"><img className="mr-3" alt="hw" width="32px" src={Logo}></img>Kantya Safira</h5>
                                            <p class="card-text">Produk terbaik yang pernah ada. Jika dibandingkan dengan harga sepeda lipat Brompton, maka harga sepeda lipat lokal terlihat murah.</p>
                                            <Stars></Stars>
                                        </div>

                                    </Card>
                                </CardDeck>
                            </div>
                        </div>
                    </StylesReview>
                </Container>
            </LayoutContent >
        )
    }
}