import React, { Component } from 'react'
import { Container, Carousel, CarouselItem } from 'react-bootstrap'
// import { checks } from '../elements/global'
import { Button } from '../elements/element'
import { History } from '../components/History'
import { Jumbotron as Jumbo } from '../components/Jumbotron'
import { Production } from '../components/Production'
import { Review } from '../components/Review'
import { NavigationBar } from '../components/NavigationBar'
import { Footer } from '../components/Footer'
import { StylesHome } from '../elements/element'
import { FontStyles } from '../elements/element'
import { CarouselImage } from '../elements/element'
import { SubHead } from '../elements/element'
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

export class Home extends Component {
    render() {
        return (
            <React.Fragment>
                <NavigationBar />
                <StylesHome>
                    <Container>
                        <div className="row">
                            <div className="col-lg-6">
                                <FontStyles brown>2020</FontStyles>
                                <FontStyles navy>Trek Maddne</FontStyles>
                                <SubHead>
                                    Trek Madone SLR sudah terlihat beda dengan produk kompetitor. Bentuknya lebih pipih besar, melanjutkan bentuk Madone 9. Handlebar dan stem-nya juga masih integrated, tapi bentuknya lebih ergonomis, dan posisi bisa di-adjust.
                        </SubHead>
                                <Button className="btn-sec" secondary spacing>DISCOVER</Button>
                            </div>
                            <div className="col-lg-6">
                                <Carousel>
                                    <CarouselItem>
                                        <CarouselImage alt="Trak Maddne"></CarouselImage>
                                    </CarouselItem>
                                </Carousel>
                            </div>
                        </div>

                        <History />

                    </Container>
                    <Jumbo />

                    <Production />
                    <Review />

                    <Footer />

                </StylesHome>
            </React.Fragment>
        )
    }

}